<template>
	<transition name="move">
	<div class="food" v-show="showFlag" ref="food">
		<div class="food-content">
			<div class="image-header">
				<img :src="food.image" alt="">
				<div class="back" @click="hide">
					<i class="iconfont icon-arrow_lift"></i>
				</div>
			</div>
			
			<div class="content">
				<h1 class="title">{{food.name}}</h1>
				<div class="detail">
					<span class="sell-count">月销{{food.sellcount}}份</span>
					<span class="rating">好评率{{food.rating}}%</span>
				</div>
				<div class="price">
					<span class="now">￥{{food.price}}</span>
					<span class="old" v-if="food.oldPrice">￥{{food.oldPrice}}</span>
				</div>
				<div class="carcontrol-wrapper">
					<carcontrol :food="food"></carcontrol>
				</div>
				<transition name="fade">
					<div @click="addFirst($event)" class="buy" v-show="!food.count || food.count ===0">
						加入购物车
					</div>
				</transition>
			</div>
			
			<split v-show="food.info"></split>
			<div class="info" v-show="food.info">
      	<h1 class="title">商品信息</h1>
      	<p class="text">{{food.info}}</p>
      </div>
      <split></split>
      
      <div class="rating">
      	<h1 class="title">商品评价</h1>
				<ratingselect :ratings="food.ratings" :selectType="selectType" :onlyContent="onlyContent"></ratingselect>
     
     		<div class="rating-wrapper">
           <ul v-show="food.ratings && food.ratings.length">
             <li v-for="rating in food.ratings" class="rating-item" v-show="needshow(rating.rateType,rating.text)">
               <div class="user">
                 <span class="name">{{rating.username}}</span>
                 <img :src="rating.avatar" width="12" height="12" alt="" class="avatar">
               </div>
               <div class="time">{{ format(rating.rateTime) }}</div>
               <p class="text">
               <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down': rating.rateType===1}"></span>
                  {{rating.text}}
             	 </p>
             </li>
           </ul>
           <div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
         </div>
         
      </div>
      	
		</div>
	</div>
	</transition>
</template>

<script>
	import BScroll from "better-scroll";
	import Vue from "vue";
	import carcontrol from "../carcontrol/carcontrol.vue";
	import split from "../split/split.vue";
	import ratingselect from "../ratingselect/ratingselect.vue";
 export default{
	 props:{
		 food:{
			 return:Object
		 }
	 },
	 data(){
		 return{
			 showFlag:false,
			 selectType:2,
			 onlyContent:false
		 }
	 },
	 methods:{
		 show(){
			 this.showFlag = true;
			 this.$nextTick(() => {
				 if(!this.scroll){
					 this.scroll = new BScroll(this.$refs.food,{
						 click:true
					 });
				 }else{
					 this.scroll.refresh();
				 }
			 })
		 },
		 hide(){
			 this.showFlag = false;
		 },
		 addFirst(event){
			 if(!event._constructed){
				 return
			 }
			 Vue.set(this.food,"count",1)
		 },
		 needshow(type,text){  //控制评论按钮的显示
			 if(this.onlyContent && !text){
				 return false;
			 }
			 if(this.selectType == 2){
				 return true
			 }else{
				 return type === this.selectType;
			 }
		 },
		 format(date){
			 var date = new Date(date);//如果date为13位不需要乘1000
  		 var Y = date.getFullYear() + '-';
  		 var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
  		 var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
  		 var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
  		 var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
  		 var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
  		 return Y+M+D+h+m+s;
		 }
	 },
	 watch:{
		 'selectType'(type){
			 this.$nextTick(() => {
				 this.scroll.refresh();
			 })
		 },
		 'onlyContent'(onlyContent){
			 this.$nextTick(() => {
				 this.scroll.refresh();
			 })
		 }
	 },
	 components:{
		 carcontrol,
		 split,
		 ratingselect
	 }
 }
</script>


<style lang="scss" scoped>
	@import "../../common/mixin.scss";
	.move-enter-active, .move-leave-active{
    transition: all 0.2s linear;
    transform: translate3D(0,0,0);
  }
  .move-enter,.move-leave-active{
    transform: translate3D(100%,0,0);
  }
	.food{
		position: fixed;
		height: 100%;
		width: 100%;
		overflow: hidden;
		background: #fff;
		.food-content{
			background: #fff;
			.image-header{
        position: relative;
        width: 100%;  
        height: 0;
        padding-top: 100%;
        img{
          position: absolute;
          left:0;
          top:0;
          width: 100%;
          height: 100%;
        }
        .back{
          position: absolute;
          top: 10px;
          left: 10px;
          background-color:rgba(0,0,0,.9);
          border-radius: 50%;
        }
        .icon-arrow_lift{
          display:block;
          padding: 10px;
          font-size: 20px;
          color:#fff;
        }
      }
			
			.content{
      	position: relative;
      	padding: 18px;
				.title{
        	line-height: 14px;
        	margin-bottom: 8px;
        	font-size: 14px;
        	font-weight: 700;
        	color: rgb(7, 17, 27);
				}		
				.detail{
        	margin-bottom: 18px;
        	line-height: 10px;
        	height: 10px;
        	font-size: 0;
					.sell-count, .rating{
          	font-size: 10px;
          	color: rgb(147, 153, 159);
					}	
					.sell-count{
          	margin-right: 12px;
					}		
				}	
				.price{
        	font-weight: 700;
        	line-height: 24px;
					.now{
          	margin-right: 8px;
          	font-size: 14px;
          	color: rgb(240, 20, 20);
					}	
					.old{
         	 	text-decoration: line-through;
          	font-size: 10px;
          	color: rgb(147, 153, 159);
					}	
				}		
				.carcontrol-wrapper{
					position: absolute;
					right: 20px;
					bottom: 20px;
					
				}
				.buy{
					position: absolute;
					right: 18px;
					bottom: 18px;
					z-index: 10;
					height: 24px;
					line-height: 24px;
					padding: 0 12px;
					box-sizing: border-box;
					border-radius: 12px;
					font-size: 10px;
					color: #fff;
					background: rgb(0, 160, 220);
					opacity: 1;
					&.fade-enter-active, &.fade-leave-active{
						transition: all 0.2s;
					}
						
					&.fade-enter, &.fade-leave-active{
						opacity: 0;
						z-index: -1;
						
					}
					
				}
				
			}	
			
			.info{
      	padding: 18px;
				.title{
        	line-height: 14px;
        	margin-bottom: 6px;
       	 	font-size: 14px;
        	color: rgb(7, 17, 27);
					}
				.text{
        	line-height: 24px;
        	padding: 0 8px;
        	font-size: 12px;
        	color: rgb(77, 85, 93);
				}
			}
			
			.rating{
      	padding-top: 18px;
				.title{
        	line-height: 14px;
        	margin-left: 18px;
        	font-size: 14px;
        	color: rgb(7, 17, 27);
				}
				
				.rating-wrapper{
        	padding: 0 18px;
        	.rating-item{
          	position: relative;
          	padding: 16px 0;
          	@include border1px(rgba(7,17,27,.1));
          	.user{
            	position: absolute;
            	right:0;
            	top:16px;
            	line-height: 16px;
            	font-size: 0;
            	.name{
              	display:inline-block;
              	margin-right:6px;
              	vertical-align: top;
              	font-size: 10px;
              	color:rgb(147,153,159);
            	}
            	.avatar{
              	border-radius:50%;
            	}
          	}
          	.time{
            	margin-bottom:6px;
            	line-height: 12px;
            	font-size: 10px;
            	color:rgb(147, 153, 159);
          	}
          	.text{
            	line-height: 16px;
            	font-size: 12px;
            	color: rgb(7,17,27);
            	.icon-thumb_up,.icon-thumb_down{
              	margin-right: 4px;
             	 line-height: 24px;
             	 font-size: 12px;
            	}
            	.icon-thumb_up{
              	color: rgb(0, 160, 220);
            	}
            	.icon-thumb_down{
              	color: rgb(147, 153, 159);
            	}
          	}
        	}
      	}
      	.no-rating{
       	 	padding: 16px 0;
        	font-size: 12px;
        	color:rgb(147, 153, 159);
      	}
			}
			
			
			
			
		}
	}
</style>


